export default {
	data() {
		return {
			orders: [],
		}
	},
	onShow() {
		this.query();
	},
	methods: {
		query() {
			this.$api.queryOrder().then(res => {
				if (res.data.code === 2000) {
					this.orders = res.data.data;
				} else {
					uni.showToast({
						title: '登陆过期，请重新登录',
						icon: 'none'
					})
				}
			})
		},
		pay(id) {
			this.$api.payment(this.orders.filter(order => order.id == id)[0]).then(res => {
				if (res.data.code === 5003) {
					uni.showToast({
						title: '登陆过期，请重新登录',
						icon: 'none'
					})
				} else if (res.data.code === 5001) {
					uni.showToast({
						title: '操作失败，系统维护中...',
						icon: 'none'
					})
				} else {
























					uni.getProvider({
						service: 'payment',
						success: (pro_res) => {
							uni.requestPayment({
								provider: pro_res.provider,
								appId: res.data.data.appId,
								nonceStr: res.data.data.nonceStr,
								timeStamp: res.data.data.timeStamp,
								package: res.data.data.packageValue,
								paySign: res.data.data.paySign,
								signType: res.data.data.signType,
								success: (pay_res) => {
									this.query();
									uni.showToast({
										title: '支付成功'
									})
								}
							})
						}
					})

				}
			})
		},
		refund(id) {
			this.$api.refund(this.orders.filter(order => order.id == id)[0]).then(res => {
				if (res.data.code === 2000) {
					uni.showToast({
						title: '申请成功',
					})
				} else {
					uni.showToast({
						title: '操作失败，系统维护中...',
						icon: 'none'
					})
				}
			})
		}
	}
}
